import React from 'react'
import HomeSketch from './HomeSketch/HomeSketch'
import Project from './Projects/Project.js'
import projects from "./Projects/data/projects.json"
import "./Home.css"
import "./HomeMediaQuery.css"

class Home extends React.Component{
	state = {
		startTransition:false,
		transitionEnded:false,
		showingProjects:false,
		projects:[]
	}


	componentDidMount(){
		this.setState({projects:projects.projects})
	}	

	transition=()=>{
		if(this.state.showingProjects===false){
			this.setState({
				startTransition:true,
				showingProjects:true
			})
		}else{
			this.setState({
				startTransition:true,
				showingProjects:false
			})
		}
			

	}
	endTransition=()=>{
		this.setState({
			startTransition:false
		})
	}

	renderProjects = () =>{
		return(		
			this.state.projects.map(project=>{
				return <Project imageSrc={require("./Projects/data/projectImages/"+project.image)} title={project.title} description={project.description} used={project.used} url={project.url} alt={"project image"}/>
			})
		)
	}
	render(){
		return(
			<div className="HomeContainer">
				<div className={this.state.showingProjects ? "SketchContainer SketchSide" : "SketchContainer"} id="SketchContainer">
					<HomeSketch startTransition = {this.state.startTransition} showingProjects={this.state.showingProjects} endTransition={this.endTransition}/>
				</div>
				<div className="InfoContainer">
					<div className="InfoName">
						有樹 - <span style={{fontFamily:"times new roman"}}>Ariki</span>
						<div className="InfoNameIntro">
							<span className="InfoText">From Tokyo, Japan. </span>
							<br/>
							<span className="InfoText">I like making things with code.<br/>I believe that code itself is a form of art.</span>
						</div>

					</div>
					<div className="InfoLinks">
							<div className="InfoProjectsLink" onMouseDown={this.transition}>{this.state.showingProjects?"Back":"Projects"}</div>
					</div>

					<div className={this.state.showingProjects && this.state.startTransition===false? "InfoProjects" : "InfoProjects_Hidden"}>
						<h2 className="ProjectsHeader">Projects</h2>
						<div className="ProjectsList">
							{this.renderProjects()}
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Home