import React from 'react'
import "./Project.css"
import "./ProjectMediaQuery.css"



const Project =props=>{
  
    return(
        <div className="ProjectContainer">
            <a href={props.url} className="ProjectImageContainer" target="_blank">
                <div className ="ProjectTextContainer">
                    <div className="ProjectDescription">{props.description}</div>
                    <div className="ProjectUsed">{props.used}</div>
                </div>
                <img className="ProjectImage" src={props.imageSrc} alt={props.alt}/>
            </a>
            <div className= "ProjectTitle">{props.title}</div>
        </div>
    )
}

export default Project