import React from 'react'
import p5 from 'p5'
import test_ from './data/mossy2.jpg'
import holes_ from './data/holes.jpg'
import vert from './data/warp.vert'
import frag from './data/warp.frag'
import "./HomeSketch.css"
class HomeSketch extends React.Component{
    sketchRef = React.createRef()

	initSketch = (p)=>{
		let w
		let h
		let ratio
		let warp
		let test
		let holes
		let mix = 0
		p.preload=()=>{
			warp = p.loadShader(vert,frag)
			test = p.loadImage(test_)
			holes = p.loadImage(holes_)
		}
		p.windowResized=()=>{
			let sketchDiv = document.getElementById("SketchContainer")
			ratio = window.devicePixelRatio
			console.log(ratio)
			w = sketchDiv.offsetWidth
			h = sketchDiv.offsetHeight
			p.resizeCanvas(w,h)
			warp.setUniform("resolution",[w,h])
			warp.setUniform("ratio",ratio)
			warp.setUniform("mix_ratio",0.0)
		}
		p.setup=()=>{
			let sketchDiv = document.getElementById("SketchContainer")
			ratio = window.devicePixelRatio
			w = sketchDiv.offsetWidth
			h = sketchDiv.offsetHeight
			p.createCanvas(w,h, p.WEBGL)
			p.shader(warp)
			warp.setUniform("resolution",[w,h])
			console.log(ratio)
			warp.setUniform("ratio", ratio)
			warp.setUniform("turq",test)
			warp.setUniform("holes", holes)
			warp.setUniform("mix_ratio",0.0)
		}

		p.draw=()=>{
			if(this.props.startTransition && this.props.showingProjects===true){
				mix+= mix*0.1+0.005
				if(mix>1){
					mix=1
					this.props.endTransition()
				}
				warp.setUniform("mix_ratio",mix)
			}else if(this.props.startTransition && this.props.showingProjects===false){
				mix-= 0.019
				if(mix<0){
					mix=0
					this.props.endTransition()
				}
				warp.setUniform("mix_ratio",mix)
			}
			warp.setUniform("time", p.millis()/1000.0+100*mix)
			warp.setUniform("mouse", [p.mouseX, p.mouseY])
			p.shader(warp)
			p.rect(-w/2,-h/2,w,h)
		}
	}

	componentDidMount(){
		this.p5Canvas = new p5(this.initSketch, this.sketchRef.current)
    }
    
	
    render(){
		
        return(
            <div ref={this.sketchRef} className="Sketch"/>
        )
    }
}


export default HomeSketch